<template>
  <div class="my-videos container mb-5">
    <h1 class="mb-5">MyVideos</h1>
    <VideoDetail v-if="isVideoSelected" />
    <li class="list-group-item video-item d-flex justify-content-between" v-for="(myVideo, idx) in myVideos" :key="idx">
      <img class="me-3" :src="`${myVideo.snippet.thumbnails.default.url}`" alt="video-thumbnail" @click="onMyVideoClick(myVideo)">
      <div class="media-body d-flex flex-column justify-content-center" @click="onMyVideoClick(myVideo)" style="width: 100%;">
        <span>
        {{ myVideo.snippet.title }}
        </span>
      </div>
      <button @click="deleteMyVideo" class="btn">X</button>
    </li>
    <h3 class="mt-5 mb-3">영상 리스트 영구 저장 방법</h3>
    <p>현재 영상 저장 기능은 임시 저장만 가능하기 때문에 영상 리스트가 삭제될 가능성이 있습니다.</p>
    <p>이런 문제를 해결할 임시방편으로 다음의 두 기능을 추가했습니다</p>
    <p><strong>1. 왼쪽의 저장 버튼을 누르고 <u>텍스트를 메모장에 복사</u>하여 영상 리스트 저장</strong></p>
    <p><strong>2. 오른쪽의 입력란에 <u>복사한 내용들을 붙여넣기</u>해서 영상 리스트 복원</strong></p>
    <!-- <p>!! 복원 시 입력 형식을 지키지 않으면 오류 발생</p>
    <p>!! 오류 발생 시 초기화 버튼 클릭</p> -->
    <div class="container mt-5">
      <div class="row">
        <div class="col">
          <li class="list-group-item clickable save-btn" @click="saveVideoList">
            저장
          </li>
          <li class="list-group-item d-flex flex-column justify-content-center" style="min-height: 71px;">
            {{ savedMyVideos }}
          </li>
        </div>
        <div class="col">
          <li class="list-group-item clickable restore-btn" @click="restoreVideoList(restoreMyVideos)">
            복원
          </li>
          <li class="list-group-item">
            <div class="input-group my-2">
              <input type="text" class="form-control" placeholder="주의! 입력 형식을 지키지 않으면 오류 발생" aria-label="Example text with button addon" aria-describedby="button-addon1" v-model="restoreMyVideos">
            </div>
          </li>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VideoDetail from '../components/VideoDetail.vue'
import { mapActions } from 'vuex'

export default {
  name: 'MyVideos',
  components: {
    VideoDetail
  },
  methods: {
    ...mapActions(['deleteMyVideo', 'onMyVideoClick', 'restoreVideoList']),
    saveVideoList() {
      this.savedMyVideos = []
      for (const savingVideo of this.$store.state.myVideos) {
        this.savedMyVideos.push(`,,,,${savingVideo.etag},,${savingVideo.id.videoId},,${savingVideo.snippet.title},,${savingVideo.snippet.description},,${savingVideo.snippet.thumbnails.default.url},,${savingVideo.snippet.thumbnails.default.width},,${savingVideo.snippet.thumbnails.default.height},,,,`)
      }
    }
  },
  computed: {
    myVideos() {
      return this.$store.state.myVideos
    },
    isVideoSelected() {
      return this.$store.getters.isVideoSelected
    },
  },
  data() {
    return {
      savedMyVideos: [],
      restoreMyVideos: [],
    }
  },
}
</script>

<style scoped>
.btn:hover {
  background-color: rgb(88, 81, 138);
}

.video-item {
  display: flex;
  cursor: pointer;
}
.video-item:hover {
  background-color:rgb(230, 255, 247);
}

.save-btn:hover {
  background-color:rgb(137, 185, 170);
}
.restore-btn:hover {
  background-color: rgb(127, 121, 172);
}
</style>