<template>
  <ul id="video-player" class="list-group detail mb-5">
    <div class="d-flex justify-content-between">
      <span>
        <i class="far fa-minus-squar ms-2 my-auto blank"></i>    
        <i class="far fa-minus-squar ms-1 my-auto blank"></i>
      </span>
      <div class="mb-1">
        <span class="clickable badge rounded-pill" :class="{ 'repeat': loop }" @click="repeatVideo">Repeat</span>
      </div>
      <span>
        <i class="far fa-minus-square me-2 my-auto add-button" @click="closeVideo"></i>
        <i class="far fa-plus-square me-1 my-auto add-button" v-if="isMyVideo" @click="addMyVideo"></i>
        <i class="fas fa-check-square me-1 my-auto add-button" v-else @click="addMyVideo"></i>
      </span>
    </div>
    <li class="list-group-item frame">
      <div class="ratio ratio-16x9">
        <iframe :src="`https://www.youtube.com/embed/${selectedVideo.id.videoId}?loop=${loop}&playlist=${selectedVideo.id.videoId}`" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      </div>
    </li>
    <li class="list-group-item fs-4 fw-bold title">
      {{ selectedVideo.snippet.title }}
    </li>
    <li class="list-group-item desc">
      {{ selectedVideo.snippet.description }}
    </li>
  </ul>
</template>

<script>
export default {
  name: 'VideoDetail',
  data() {
    return {
      loop: 0
    }
  },
  methods: {
    repeatVideo() {
      this.loop = this.loop === 0 ? 1 : 0
    },
    addMyVideo() {
      this.$store.dispatch('addMyVideo', this.selectedVideo,)
    },
    closeVideo() {
      this.$store.dispatch('closeVideo')
    }
  },
  computed: {
    selectedVideo() {
      return this.$store.state.SelectedVideo
    },
    isMyVideo() {
      let ismyvideo = true
      if (Object.keys(this.selectedVideo).length) {
        const id = this.selectedVideo.id.videoId
        if (this.$store.state.myVideos.length) {
          const idx = this.$store.state.myVideos.findIndex(function(myVideo) {
            return myVideo.id.videoId === id
          })
          if (idx != -1) {
            ismyvideo = false
          }
        }
      }
      return ismyvideo
    },
  },
}
</script>

<style>
.title {
  background-color: rgb(233, 255, 244);
}

.desc {
  background-color:rgb(213, 213, 236)
}

.badge {
  width: 80px;
  background-color: rgb(74, 207, 152);
}

.badge:hover {
  color: rgb(88, 81, 138);
}

.repeat {
  color: rgb(88, 81, 138);  
}

.repeat:hover {
  color: rgb(255, 255, 255);
}

.add-button:hover {
  cursor: pointer;
  color: rgb(118, 112, 167);
}

.blank {
  color: white;
}
</style>
