import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [createPersistedState()],
  state: {
    videos: [],
    SelectedVideo: {},
    videosR: [],
    pageSearch: 5,
    pageRelated: 5,
    myVideos: [],
  },
  getters: {
    isVideoSelected: state => !!Object.keys(state.SelectedVideo).length,
    isVideoSearched: state => !!state.videos.length,
  },
  mutations: {
    SEARCH_VIDEO: (state, videos) => { state.videos = videos },
    SELECT_VIDEO: (state, video) => { state.SelectedVideo = video },
    RESET: (state) => {
      state.SelectedVideo = {}
      state.videos= []
    },
    RELATED_VIDEO: (state, videos) => { state.videosR = videos },
    MORE_VIDEOS: state => state.pageSearch += 5,
    MORE_VIDEOS_R: state => state.pageRelated += 5,
    RESET_PAGE: state => state.pageSearch = 5,
    RESET_PAGE_R: state => state.pageRelated = 5,
    ADD_MY_VIDEO: (state, video) => { state.myVideos.push(video) },
    DELETE_MY_VIDEO: (state, idx) => { state.myVideos.splice(idx,1) },
    CLOSE_VIDEO: state => state.SelectedVideo = '',
  },
  actions: {
    searchVideo({ commit }, query) {
      axios.get('https://www.googleapis.com/youtube/v3/search', {
        params: {
          key: process.env.VUE_APP_YOUTUBE_API_KEY,
          type: 'video',
          part: 'snippet',
          q: query,
          maxResults: 20
        },
      })
        .then(res => commit('SEARCH_VIDEO', res.data.items))
        .catch(err => console.error(err))

      commit('RESET_PAGE')
    },

    onVideoClick({ commit }, video) {
      commit('SELECT_VIDEO', video)
      // 화면 이동
      setTimeout(function() {
        const location = document.querySelector("#video-player").offsetTop;
        window.scrollTo({top:location, behavior:'smooth'});
      },0)
      // 관련 영상 찾기
      axios.get('https://www.googleapis.com/youtube/v3/search', {
        params: {
          key: process.env.VUE_APP_YOUTUBE_API_KEY,
          type: 'video',
          part: 'snippet',
          relatedToVideoId: video.id.videoId,
          maxResults: 20
        },
      })
        .then(res => commit('RELATED_VIDEO', res.data.items))
        .catch(err => console.error(err))

        commit('RESET_PAGE_R')
    },

    onMyVideoClick({ commit }, video) {
      commit('SELECT_VIDEO', video)
      // 화면 이동
      setTimeout(function() {
        const location = document.querySelector("#video-player").offsetTop;
        window.scrollTo({top:location, behavior:'smooth'});
      },0)
    },

    Reset({ commit }) {
      commit('RESET')
    },

    onMoreVideo({ commit }) {
      commit('MORE_VIDEOS')
    },

    onMoreVideoR({ commit }) {
      commit('MORE_VIDEOS_R')
    },

    addMyVideo({ commit }, video) {
      if (this.state.myVideos.length) {
        const idx = this.state.myVideos.findIndex(function(myVideo) {
          return myVideo.id.videoId === video.id.videoId
        })
        if (idx === -1) {
          // commit('DELETE_MY_VIDEO', idx)
          commit('ADD_MY_VIDEO', video)
        } else {
          commit('DELETE_MY_VIDEO', idx)
        }
      } else {
        commit('ADD_MY_VIDEO', video)
      }
    },

    deleteMyVideo({ commit }, idx) {
      commit('DELETE_MY_VIDEO', idx)
    },

    closeVideo({ commit }) {
      commit('CLOSE_VIDEO')
    },

    restoreVideoList({ commit }, videosStr) {
      const videos = videosStr.split(',,,,')
      for (const video of videos) {
        if (video.length > 6) {
          const converted = video.split(',,')
          const newVideo = {etag: converted[0], id: {videoId: converted[1]}, snippet: {title: converted[2], description: converted[3], thumbnails: {default: {url: converted[4], width: converted[5], height: converted[6]}}}}
          
          if (this.state.myVideos.length) {
            const idx = this.state.myVideos.findIndex(function(myVideo) {
              return myVideo.id.videoId === newVideo.id.videoId
            })
            if (idx === -1) {
              commit('ADD_MY_VIDEO', newVideo)
            } 
          } else {
            commit('ADD_MY_VIDEO', newVideo)
            }
        }
      }
    },
  },
  modules: {
  }
})
